import React from 'react';
import axios from 'axios';
import Movie from '../components/Movie';
import './Home.css';

class Home extends React.Component{
    constructor(data) {
        super(data);
        this.state = {
            is_loading : true,
            movies : [],
        };
    }

    //API로 movie 목록을 가져오는 함수
    get_movies = async () => {
        const result = await axios.get("https://yts-proxy.now.sh/list_movies.json?sort_by=rating");
        const movies = result.data.data.movies;
        //key와 value명이 같으면 movies 처럼 하나로 표현 가능
        this.setState({movies , is_loading : false});
    }

    //component가 실행될때 실행
    componentDidMount(){
        this.get_movies();
    }

    render(){
        const {is_loading, movies} = this.state;
        return (
        <section className="container">
            {is_loading ? 
                //로딩중일때
                (
                    <div className="loader">
                        <span className="loader_text">Loading...</span>
                    </div> 
                )
                : 
                //로딩이 끝나고 영화목록 컴포넌트를 실행
                (
                    <div className="movie_container">
                        {movies.map(movie => {
                            return <Movie 
                                key={movie.id} 
                                id={movie.id} 
                                year={movie.year} 
                                title={movie.title} 
                                summary={movie.summary} 
                                medium_cover_image={movie.medium_cover_image}
                                genres={movie.genres} 
                            />;
                        })}
                    </div>
                )
            }
        </section>
        )
    }
}

export default Home;