import React from 'react';
import { Link } from 'react-router-dom';
import './Movie_detail.css';

class Movie_detail extends React.Component {
    componentDidMount(){
        const {location, history} = this.props;
        if(typeof(location.state) === "undefined"){
            history.push("/");
        }
    }

    render(){
        const {location} = this.props;
        let data = location.state;
        if(data){
            return (
                <section className="movie_section">
                    <Link to={
                    {
                        pathname : "/",
                    }
                    }>
                        <button className="detail_btn" type="button">Back</button>
                    </Link>
                    <div className="movie">
                        <img className="movie_img" src={data.medium_cover_image} alt={data.title} title={data.title} />
                        <div className="movie_data">
                            <h3 className="movie_title">{data.title}</h3>
                            <h5 className="movie_year">{data.year}</h5>
                            <ul className="genres">
                                {data.genres.map((genre, index) => {
                                    return (
                                        <li key={index} className="genre">
                                            {genre}
                                        </li>
                                    )
                                })}
                            </ul>
                            <p className="movie_summary">
                                {data.summary}
                            </p>
                        </div>
                    </div>
                </section>
            )
        }else{
            return null;
        }
    }
}

export default Movie_detail;