import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import './Movie.css';

function Movie({id, year, title, summary, medium_cover_image, genres}){
    const summary_length = summary.slice(0, 200).length;
    let detail_btn_display = {
        display: "none",
    };
    let summary_add_text = "";
    if(summary_length >= 200){
        detail_btn_display.display = "";
        summary_add_text = "...";
    }

    return (
        <div className="movie" id={id}>
            <img className="movie_img" src={medium_cover_image} alt={title} title={title} />
            <div className="movie_data">
                <h3 className="movie_title">{title}</h3>
                <h5 className="movie_year">{year}</h5>
                <ul className="genres">
                    {genres.map((genre, index) => {
                        return (
                            <li key={index} className="genre">
                                {genre}
                            </li>
                        )
                    })}
                </ul>
                <p className="movie_summary">
                    {summary.slice(0, 200) + summary_add_text}
                </p>
                <Link to={
                    {
                        pathname : "/movie_detail/" + id,
                        state : {
                            id,
                            year,
                            title,
                            summary,
                            medium_cover_image,
                            genres,
                        }
                    }
                }>
                    <button className="detail_btn" style={detail_btn_display} type="button">Detail</button>
                </Link>
            </div>
        </div>
    )
}

//Movie Component에 들어오는 변수 유효성 검사
//isRequired는 데이터가 반드시 있어야 되고
//데이터 Type만 지정되어 있으면 없어도 되지만 Type이 다르면 Error
Movie.prototype = {
    id : PropTypes.number.isRequired, //Key 값
    year : PropTypes.number.isRequired, //년도
    title : PropTypes.string.isRequired, //영화 제목
    summary : PropTypes.string.isRequired, //영화 소개
    medium_cover_image : PropTypes.string.isRequired, //포스터 이미지
    genres : PropTypes.arrayOf(PropTypes.string).isRequired, //영화 종류
}

export default Movie;